import { DeepPartial, LocalesMock } from '../core'
import { VaultCategory } from './vault-category.model'

/**
 * Vault Type Mock
 */
export function VaultCategoryMock(
  options?: DeepPartial<VaultCategory>
): VaultCategory {
  const now = new Date().toISOString()

  return {
    _id: options?._id || 'VAULT_CATEGORY_ID',
    code: options?.code || 'VAULT_CATEGORY_CODE',
    name: LocalesMock(options?.name),
    description: LocalesMock(options?.description),
    createdAt: now,
    createdBy: options?.createdBy || 'USER_ID',
    updatedAt: now,
    updatedBy: options?.updatedBy || 'USER_ID',
  }
}
